.TodoSearch {
  background: #F9FBFC;
  border-radius: 10px;
  border: 2px solid #202329;
  margin: 0 24px 20px;
  height: 64px;
  width: calc(100% - 50px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: #a5a5a5;
  font-family: sans-serif;
  font-weight: 400;
}
.TodoSearch:focus{
  outline: thick double #61dafa;
}
.TodoSearch:disabled {
  opacity: 25%;
}


